.about-us-wrapper {
  margin-bottom: 115px;
  @include r(1024) {
    margin-bottom: 100px; }
  @include r(768) {
    margin-bottom: 90px; }
  .subheader {
    @include quotedesktop;
    margin-bottom: 40px;
    max-width: 730px;
    @include r(1024) {
      @include quotetablet;
      max-width: 707px;
      margin-bottom: 60px; }
    @include r(768) {
      max-width: 100%; }
    @include r(375) {
      @include quotemobile;
      margin-bottom: 20px; } }
  .text-container {
    padding-left: 220px;
    padding-right: 330px;
    @include textdesktop;
    color: $black2;
    @include r(1024) {
      @include texttablet;
      padding-left: 0;
      padding-right: 210px; }
    @include r(768) {
      padding-right: 0; }
    @include r(375) {
      @include textmobile; } }
  h3 {
    @include h3desktop;
    margin-top: 40px;
    margin-bottom: 30px;
    @include r(1024) {
      @include h3tablet; }
    @include r(375) {
      @include h3mobile;
      margin-top: 30px;
      margin-bottom: 25px; } }
  ul {
    padding-left: 110px;
    @include r(1024) {
      padding-left: 84px; }
    @include r(768) {
      padding-left: 80px; }
    @include r(375) {
      padding-left: 40px; }
    li {
      @include maw(400); } }
  .img-wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 80px;
    margin-bottom: 80px;
    padding-left: 100px;
    padding-right: 100px;
    @include r(1300) {
      padding-left: 0;
      padding-right: 0; }
    @include r(1024) {
      margin-top: 60px;
      margin-bottom: 60px; }
    @include r(768) {
      flex-direction: column;
      margin-bottom: 0;
      margin-top: 40px;
      margin-bottom: 40px; }
    .img {
      width: 510px;
      height: 360px;
      background-size: cover;
      background-position: center center;
      background-repeat: no-repeat;
      &:last-of-type {
        @include r(768);
        margin-bottom: 0; }
      @include r(1300) {
        width: 45%;
        height: 330px; }
      @include r(768) {
        width: 100%;
        height: 360px;
        margin-bottom: 30px; }
      @include r(375) {
        height: 240px; } } } }

