/* ============================================================================
 * FONTS
 * ============================================================================ */
@include font(regular, 'Montserrat-Regular');
@include font(medium, 'Montserrat-Medium');
@include font(semibold, 'Montserrat-SemiBold');
@include font(bold, 'Montserrat-Bold');

@mixin fr {
  font-family: regular, sans-serif; }
@mixin fm {
  font-family: medium, sans-serif; }
@mixin fs {
  font-family: semibold, sans-serif; }
@mixin fb {
  font-family: bold, sans-serif; }

@mixin h1desktop {
  @include fm;
  font-size: 48px;
  line-height: 1; }
@mixin textdesktop {
  @include fr;
  font-size: 18px;
  line-height: 1.2; }
@mixin h2desktop {
  @include fm;
  font-size: 36px;
  line-height: 1; }
@mixin buttontext {
  @include fs;
  font-size: 14px;
  line-height: 1; }
@mixin h3desktop {
  @include fm;
  font-size: 24px;
  line-height: 1.2; }
@mixin quotedesktop {
  @include fm;
  font-size: 28px;
  line-height: 1.2; }
@mixin h4desktop {
  @include fm;
  font-size: 14px;
  line-height: 1; }
@mixin h1tablet {
  @include fm;
  font-size: 40px;
  line-height: 1; }
@mixin h4tablet {
  @include fm;
  font-size: 14px;
  line-height: 1.2; }
@mixin quotetablet {
  @include fm;
  font-size: 24px;
  line-height: 1; }
@mixin texttablet {
  @include fr;
  font-size: 16px;
  line-height: 1.2; }
@mixin h3tablet {
  @include fm;
  font-size: 20px;
  line-height: 1.2; }
@mixin h2tablet {
  @include fm;
  font-size: 30px;
  line-height: 1; }
@mixin quotemobile {
  @include fm;
  font-size: 16px;
  line-height: 1.2; }
@mixin textmobile {
  @include fr;
  font-size: 14px;
  line-height: 1.2; }
@mixin h3mobile {
  @include fm;
  font-size: 16px;
  line-height: 1.2; }
@mixin h1mobile {
  @include fm;
  font-size: 28px;
  line-height: 1.2; }
@mixin h2mobile {
  @include fm;
  font-size: 20px;
  line-height: 1; }
@mixin h4mobile {
  @include fm;
  font-size: 13px;
  line-height: 1.2; }
@mixin menudesktop {
  @include fb;
  font-size: 13px;
  line-height: 1; }

@mixin Menu_text_desktop {
  font-family: bold;
  font-size: 13px;
  text-transform: uppercase; }
