.products-page-wrapper {
  .container {
    overflow-x: hidden;
    .products-wrapper {
      margin-bottom: 50px;
      @include r(375) {
        margin-bottom: 30px; } } } }
.products-page-advantages {
  margin-bottom: 50px;
  @include r(1024) {
    margin-bottom: 20px; }
  h2 {
    @include fm;
    font-size: 36px;
    line-height: 1;
    color: #000000;
    margin: 80px 0 110px;
    @include r(1024) {
      font-size: 24px;
      margin: 100px 0 80px; }
    @include r(768) {
      margin: 90px 0 60px; }
    @include r(375) {
      font-size: 18px;
      margin: 33px 0 60px; } }
  .advantages {
    padding-left: 220px;
    padding-right: 110px;
    @include r(1070) {
      padding-left: 0;
      padding-right: 0; }
    .advantage {
      width: 30%;
      margin-right: 20px;
      @include r(768) {
        width: 45%; }
      @include r(550) {
        width: 100%;
        margin-right: 0; }
      .advantage__body {
        color: $gray2;
        @include fr;
        line-height: 1.2;
        font-size: 16px;
        margin-top: 20px;
        @include r(375) {
          font-size: 14px; } } } } }
