html {
  font-size: 10px; }
body {
  background: #FFF;
  // +css-lock(16, 30, 600, 1280)
  line-height: 1.4;
  font-family: regular;
  margin-top: 80px;
  @include r(1300) {
    margin-top: 109px; }
  @include r(700) {
    margin-top: 71px; }
 }  // +miw(320)

::selection {
  background: #ccc; }
::-moz-selection {
  background: #ccc; }
::-webkit-selection {
  background: #ccc;
  color:#fff {} }

.clearfix::after {
  display: table;
  content: "";
  clear: both; }

a {
  text-decoration: none;
  color: #000; }

.container {
  @include maw(1280);
  margin: 0 auto;
  width: 100%;
  @include r(1300) {
    padding-left: 32px;
    padding-right: 32px; }
  @include r(1024) {
    padding-left: 24px;
    padding-right: 24px; }
  @include r(375) {
    padding-left: 20px;
    padding-right: 20px; } }
/* ============================================================================
 * START Product item
 * ============================================================================ */
.products-whole-wrapper {
  background-color: $gray9;
  padding-top: 76px;
  padding-bottom: 110px;
  @include r(1024) {
    padding-top: 60px;
    padding-bottom: 20px; }
  @include r(550) {
    padding-top: 40px; } }
.products-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-left: -20px;
  margin-right: -20px;
  @include r(684) {
    justify-content: center; } }
.product-item {
  margin-bottom: 50px;
  position: relative;
  width: 290px;
  height: 390px;
  border-radius: 7px;
  padding: 20px;
  border: 1px solid $gray7;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin-left: 20px;
  margin-right: 20px;
  @include trans;
  @include r(1024) {
    margin-bottom: 40px; }
  @include r(375) {
    margin-bottom: 30px; }
  &:hover {
    background-color: $green1;
    .product-item__header {
      color: #fff; }
    svg {
      margin-bottom: 100px;
      circle {
        fill: #fff; }
      path {
        fill: #3ECC29; } }
    .product-item__image {
      bottom: -100%; } }
  svg {
    height: 30px;
    width: 30px;
    align-self: flex-end;
    margin-bottom: 25px;
    @include trans;
    circle {
      @include trans; }
    path {
      @include trans; } }
  .product-item__header {
    @include fm;
    font-size: 20px;
    @include trans; }
  .product-item__image {
    height: 200px;
    width: 200px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 160px;
    @include trans; } }
/* ============================================================================
 * END Product item
 * ============================================================================ */
/* ============================================================================
 * START Advantages
 * ============================================================================ */
.advantages {
  display: flex;
  flex-wrap: wrap;
  @include r(900) {
    justify-content: space-between; }
  @include r(504) {
    justify-content: center; }
  .advantage {
    width: 290px;
    margin-bottom: 100px;
    @include r(1300) {
      width: 220px; }
    @include r(1024) {
      margin-bottom: 80px; }
    .advantage__img {
      height: 50px;
      margin-bottom: 30px;
      background-size: contain;
      background-repeat: no-repeat; }
    .advantage__header {
      @include fs;
      font-size: 18px; } } }
/* ============================================================================
 * END Advantages
 * ============================================================================ */
/* ============================================================================
 * START Forms
 * ============================================================================ */
 ::-webkit-input-placeholder {
  color: $gray4; }

 :-ms-input-placeholder {
  color: $gray4; }

 ::-moz-placeholder {
  color: $gray4;
  opacity: 1; }

 :-moz-placeholder {
  color: $gray4;
  opacity: 1; }
/* ============================================================================
 * END Forms
 * ============================================================================ */
/* ============================================================================
 * START UL
 * ============================================================================ */
ul {
  li {
    position: relative;
    margin-bottom: 30px;
    &:before {
      content: '';
      position: absolute;
      left: -38px; // 28 + 8
      top: 5px;
      height: 8px;
      width: 8px;
      border-radius: 50px;
      background-color: $green1; }
    &:after {
      content: '';
      position: absolute;
      left: -42px;
      top: 1px;
      height: 16px;
      width: 16px;
      border-radius: 50px;
      background-color: transparent;
      border: 1px solid $green1; } } }
header,
footer {
  ul {
    li {
      margin-bottom: 0;
      &:before,
      &:after {
        display: none; } } } }
/* ============================================================================
 * END UL
 * ============================================================================ */
/* ============================================================================
 * START Breadcrambs
 * ============================================================================ */
.breadcrambs {
  color: $gray3;
  @include h4desktop;
  padding-top: 40px;
  margin-bottom: 10px;
  @include r(1024) {
    @include h4tablet; }
  @include r(375) {
    padding-top: 30px; } }
h1.breadcrambs-h1 {
  @include h1desktop;
  margin-bottom: 100px;
  @include maw(510);
  @include r(1024) {
    @include h1tablet;
    max-width: 100%; }
  @include r(375) {
    @include h1mobile;
    margin-bottom: 60px; } }
/* ============================================================================
 * END Breadcrambs
 * ============================================================================ */
